import styled from "styled-components";
import { BoxContentCenter } from "../Box";

export const Button = styled(BoxContentCenter).attrs({
  as: "button",
})`
  width: 48px;
  height: 48px;

  border-radius: 50%;

  background-color: ${(props) => props.theme.color.ultramarineBlue};

  transition: opacity 150ms ease-in;
  cursor: pointer;

  &:hover {
    background-color: rgba(0, 0, 0, 0.04);
  }
`;
